import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Button, Container } from "./styled";
import {
  toogleHideDone,
  setAllDone,
  selectAreTasksEmpty,
  selectIsEveryTaskDone,
  selectHideDone,
  removeAllTasks,
  selectAreTasksUndone,
} from "../../tasksSlice";

const Buttons = () => {
  const areTaskEmpty = useSelector(selectAreTasksEmpty);
  const areTaskUndone = useSelector(selectAreTasksUndone);
  const isEveryTaskDone = useSelector(selectIsEveryTaskDone);
  const hideDone = useSelector(selectHideDone);

  const dispatch = useDispatch();

  return (
    <Container>
      {!areTaskEmpty > 0 && (
        <>
          <Button
            onClick={() => dispatch(toogleHideDone())}
            disabled={areTaskUndone}
          >
            {hideDone ? "Poka≈º" : "Ukryj"} uko≈Ñczone
          </Button>
          <Button
            onClick={() => dispatch(setAllDone())}
            disabled={isEveryTaskDone}
          >
            Uko≈Ñcz wszystkie
          </Button>
          <Button onClick={() => dispatch(removeAllTasks())}>
            Usu≈Ñ wszystkie
          </Button>
        </>
      )}
    </Container>
  );
};
export default Buttons;
